import React, { Component, Suspense, lazy } from 'react';
import { HashRouter, Switch, Route } from 'react-router-dom';
//import createBrowserHistory from 'history/createBrowserHistory';

//import Layout1 from '../layouts/Layout1.js';
//import Layout2 from '../layouts/Layout2.js';

//import Hello1 from '../pages/Hello1.jsx';
// import Hello2 from '../pages/Hello2.jsx';
// import Hello3 from '../pages/Hello3.jsx';
// import Hello4 from '../pages/Hello4.jsx';

// 使用路由懒加载
const Hello5 = lazy(() => import( /*webpackChunkName: "router-Hello5"*/  '../pages/Hello5.jsx') );
const Hello6 = lazy(() => import( /*webpackChunkName: "router-Hello6"*/  '../pages/Hello6.jsx') );
const Cycle1 = lazy(() => import( /*webpackChunkName: "router-Cycle1"*/  '../pages/Cycle1.jsx') );
const Cycle2 = lazy(() => import( /*webpackChunkName: "router-Cycle2"*/  '../pages/Cycle2.jsx') );
const RefTest1 = lazy(() => import( /*webpackChunkName: "router-RefTest1"*/  '../pages/RefTest1.jsx') );
const HooksTest1 = lazy(() => import( /*webpackChunkName: "router-HooksTest1"*/  '../pages/HooksTest1.jsx') );
const HooksTest2 = lazy(() => import( /*webpackChunkName: "router-HooksTest2"*/  '../pages/HooksTest2.jsx') );
const ReactData1 = lazy(() => import( /*webpackChunkName: "router-ReactData1"*/  '../pages/reactdata/ReactData1.jsx') );
const Optimize01 = lazy(() => import( /*webpackChunkName: "router-Optimize01"*/  '../pages/optimize/Optimize01.jsx') );
const Props1 = lazy(() => import( /*webpackChunkName: "router-Optimize01"*/  '../pages/props/Props1.jsx') );
const StateTest = lazy(() => import( /*webpackChunkName: "router-Optimize01"*/  '../pages/state/StateTest.jsx') );
const Rtest01 = lazy(() => import( /*webpackChunkName: "router-Optimize01"*/  '../pages/routertest/Rtest01.jsx') );
const Rtest02 = lazy(() => import( /*webpackChunkName: "router-Optimize01"*/  '../pages/routertest/Rtest02.jsx') );
const Heightclass = lazy(() => import( /*webpackChunkName: "router-Optimize01"*/  '../pages/heightclass/Heightclass.jsx') );
//const Hello6 = lazy(() => import('../pages/Hello6.jsx') );


const otherRouter = [
    { path:"/", component: Cycle1,},
    { path:"/Hello5", component: Hello5,},
    { path:"/Hello6", component: Hello6,},
    { path:"/Cycle1", component: Cycle1,},
    { path:"/Cycle2", component: Cycle2,},
    { path:"/RefTest1", component: RefTest1,},
    { path:"/HooksTest1", component: HooksTest1,},
    { path:"/HooksTest2", component: HooksTest2,},
    { path:"/ReactData1", component: ReactData1,},
    { path:"/Optimize01", component: Optimize01,},
    { path:"/Props1", component: Props1,},
    { path:"/StateTest", component: StateTest,},
    { path:"/Rtest01", component: Rtest01,},
    { path:"/Rtest02", component: Rtest02,},
    { path:"/Heightclass", component: Heightclass,},
];

export default class RouteConfig extends Component{
    render(){
        return(
            <div>
                <HashRouter>
                    <Suspense fallback={<div>Loading...</div>}>
                        <Switch>
                            { otherRouter.map(item => <Route exact path={item.path}  component={item.component} key={item.path}/>) }
                        </Switch>
                    </Suspense>
                </HashRouter>
            </div>
        )
    }
};